import ReactModal from 'react-modal'
import React, { useState } from 'react'

import Loader from './loader'

import { loadImage, useEffectSafe } from '../utils'

import './modal.css'

const modalStyles = {
  content: {
    display: 'flex',
    border: 'none',
    background: 'rgba(23, 20, 29, 0.95)'
  },
  overlay: { zIndex: 4, backgroundColor: 'rgba(0, 0, 0, 0.5)' }
}

export const Modal = ({ photo, modalIsOpen, closeModal }) => {
  const [isLoaded, setIsLoaded] = useState(false)

  useEffectSafe(
    async () => (photo ? loadImage(photo.url, { delay: 1000 }) : null),
    () => photo && setIsLoaded(true)
  )

  return (
    <ReactModal
      style={modalStyles}
      isOpen={modalIsOpen}
      contentLabel="Photo Modal"
      onRequestClose={closeModal}
      appElement={document.getElementById('root')}
    >
      <div className="Modal-dismiss" onClick={closeModal} />
      <div className="Modal-photo">
        {isLoaded ? (
          <img src={photo.url} alt={photo.title} />
        ) : (
          <div className="Modal-loader">
            <Loader size={60} />
          </div>
        )}
      </div>
    </ReactModal>
  )
}

export default Modal
